import React from 'react'
import './css/Response.css';

export default function Response( {query} ) {

    return (
        <> 
            <div class="col clarification-container mb-3 p-3">
                <h4>Non-Technical Clarification {query.id}</h4>
                <div class="clarification">{query.clarification}</div>
                <div class="response">{query.response}</div>
            </div> 
        </>
    )
}
