import React, { useState, useRef, useEffect } from 'react'; 
import ClarificationList from './ClarificationList';
import ResponseList from './ResponseList';
import Modal from "react-bootstrap/Modal";
import "bootstrap/dist/css/bootstrap.min.css";
import './css/style.css';  

function App() {
  const [clarification, setClarification] = useState([])  
  const builderValueRef = useRef(); 
  const responseValueRef = useRef();

  const SESSION_STORAGE_KEY = 'react-clarification-converter.clarifications'

  useEffect(() => {
    const storedClarification = JSON.parse(sessionStorage.getItem(SESSION_STORAGE_KEY))
    if (storedClarification) setClarification(storedClarification)
  }, [])

  useEffect(() => {
    sessionStorage.setItem(SESSION_STORAGE_KEY, JSON.stringify(clarification))
  }, [clarification])

  function addClarification(e){ 
    const clarificationValue = builderValueRef.current.value;
    const responseValue = responseValueRef.current.value;
    if (clarificationValue === "" || responseValueRef === '') return 
    setClarification((prevClarifications) => {   
      return [...prevClarifications, { 
        id: clarification.length+1, 
        clarification: clarificationValue, 
        response: responseValue}]
    })
    builderValueRef.current.value = null;
    responseValueRef.current.value = null;
  }

  function resetClarifications(e){
    setClarification([]);
    builderValueRef.current.value = null;
    responseValueRef.current.value = null;
  }

  const [isOpen, setIsOpen] = React.useState(false);

  const showModal = () => { 
    setIsOpen(true); 
  };

  const hideModal = () => {
    setIsOpen(false);
  };

  function getCopy(){ 
    let responseText = `Hello Team!

*{color:#3F50B1}Clarification:{color}*`;

    let index = 1;

    clarification.forEach((query) => {
      responseText += `
{panel:title=Non-Technical Clarification ${index.toString()}  } 
${query.clarification}
{color:#14892c}Clarification: ${query.response}
{color} 
{panel}
`
      index++;
    })

    responseText += `
_*{color:#3F50B1}Thank you{color}*_`

    document.getElementById('response-textarea').value = responseText; 
    copyText();
  }
 
  const copyText = () => {  
    const responseText = document.getElementById('response-textarea'); 
    responseText.select(); 
    document.execCommand("copy");
  }

  return (
    <>
        <main className="text-center container">
            <h1 className="banner p-3">Enter Fields</h1>
            <hr />
            <ClarificationList clarification={clarification}/>  
            <hr />
            <h4 className="p-1">Add Non-Technical Clarification </h4>
            <div className="row">
                <div className="col">
                    <div className="input-group mb-3">
                        <div className="input-group-prepend">
                            <span className="input-group-text">Clarification</span>
                        </div>
                        <textarea ref={builderValueRef} className="form-control" aria-label="Query"></textarea>
                    </div>
                    <div className="input-group mb-3">
                        <div className="input-group-prepend">
                            <span className="input-group-text">Response</span>
                        </div>
                        <textarea ref={responseValueRef} className="form-control" aria-label="response"></textarea>
                    </div>
                </div>
            </div>
            <button onClick={resetClarifications} type="button" className="btn btn-danger m-3">Reset</button>
            <button onClick={addClarification} type="button" className="btn btn-primary m-3">Add</button>
            <button onClick={showModal} type="button" className="btn btn-secondary m-3">Done</button>
 
            <Modal show={isOpen} onHide={hideModal} scrollable={true}>
              <Modal.Header>
                <Modal.Title>Clarification Response</Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <ResponseList clarification={clarification}/> 
                <textarea id="response-textarea" className="form-control"></textarea>

              </Modal.Body>
              <Modal.Footer>
                <button onClick={hideModal} className='btn btn-secondary'>Cancel</button>
                <button onClick={getCopy} className="btn btn-primary">Copy</button>
              </Modal.Footer>
            </Modal>
        </main>
    </>
  )
}

export default App;
