import React from 'react'

export default function Clarification( {query} ) { 
    return ( 
        <>
            <h4 className="p-1">Non-Technical Clarification {query.id}</h4>
            <div className="row">
                <div className="col">
                    <div className="input-group mb-3">
                        <div className="input-group-prepend">
                            <span className="input-group-text">Clarification</span>
                        </div>
                        <textarea className="form-control" aria-label="Query">{query.clarification}</textarea>
                    </div>
                    <div className="input-group mb-3">
                        <div className="input-group-prepend">
                            <span className="input-group-text">Response</span>
                        </div>
                        <textarea className="form-control" aria-label="response">{query.response}</textarea>
                    </div>
                </div>
            </div>
        </>
    )
}
